#root {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

@page {
  margin: 0;
}

@mixin noprint {
  @media print {
    display: none !important;
  }
}

header {
  @include noprint;
}

.toolbar {
  justify-content: space-between;
}

main {
  text-align: center;
  padding: 10px;
  flex: 1;
}

.generator-config {
  @include noprint;
  max-width: 400px;
  margin: auto;
  > * {
    margin: 5px !important;
  }
}

.code-list {
  list-style-type: none;
  max-width: 900px;
  margin: auto;
  padding: 0;
}

.code-list-item {
  display: inline-block;
  border: 1px dashed rgb(212, 212, 212);
  padding: 0;
  margin: 0;
  font-weight: bold;
}

footer {
  text-align: center;
  margin: 10px 50px;
  font-weight: bold;
  @include noprint;
}
